.oneChoisePictureWrapper {
  margin: 30px 0 0 0;
  width: 80%;
  height: 100%;
  justify-content: center;
  align-content: flex-start;
  align-items: flex-start;
}

.oneChoisePicture {
  user-select: none;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
  transition: all 0.2s ease;
}

.oneChoisePictureCard {
  user-select: none;
  margin: 10px;
  background-color: #ffffff;
  border: none;
  padding: 2px 2px;
  position: relative;
  box-shadow: -5px -5px 15px #ffffff, 5px 5px 15px #e9ecf4;
  border-radius: 8px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  transition: all 0.2s ease;
}

.oneChoisePictureCard img {
  pointer-events: none;
  max-width: 10vw;
  border-radius: 12px;
}

.oneChoisePictureCard:hover {
  box-shadow: -5px -5px 15px #b8b7b7, 5px 5px 15px #99999b;
  cursor: url(img/legCursorMin.png), auto;
  transition: all 0.2s ease;
}

.oneChoisePictureCard:active {
  box-shadow: -5px -5px 15px #ecebeb, 5px 5px 15px #c3c6ce;
  cursor: url(img/legCursorActive.png), auto;
  transition: all 0.2s ease;
}
.oneChoisePictureCardActive {
  box-shadow: none;
  outline: 7px solid #1d65ffa4;
  transition: all 0.2s ease;
}
