.dnd_matchingWrapper {
  width: 100%;
  height: 80%;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
}

.dropPlace {
  height: 65%;
  width: 80%;
  align-items: center;
  justify-content: center;
  display: flex;
}

.dragPlace {
  height: 30%;
  width: 80%;
  align-items: center;
  justify-content: space-around;
  display: flex;
}

.dragPlace img {
  width: 15%;
  border-radius: 12px;
}

.dragPlace img:hover {
  box-shadow: -5px -5px 15px #b8b7b7, 5px 5px 15px #99999b;
  cursor: url(img/legCursorMin.png), auto;
  transition: all 0.2s ease;
}

.dragPlace img:active {
  box-shadow: -5px -5px 15px #ecebeb, 5px 5px 15px #c3c6ce;
  cursor: url(img/legCursorActive.png), auto;
  transition: all 0.2s ease;
}

.dragPictureActive {
  outline: 7px solid #1d65ffa4;
}
