/* actual styles*/

.chronoDndWrapper {
  user-select: none;
  width: 100%;
  height: 85%;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
  /* border: 1px solid black; */
}

.cronoDropPlace {
  /* border: 1px solid black; */
  height: 65%;
  width: 100%;
  align-items: center;
  justify-content: space-around;
  display: flex;
}

.chronoDragPlace {
  width: 100%;
  height: 35%;
  align-items: center;
  justify-content: space-around;
  display: flex;
}

.chronoDragPicture:hover {
  box-shadow: -5px -5px 15px #b8b7b7, 5px 5px 15px #99999b;
  cursor: grab;
  transition: all 0.2s ease;
}

