.chronoDropBox {
  position: relative;
  width: 300px;
  height: 209px;
  outline: 5px dashed #b6b6b6;
  border-radius: 12px;
  background-color: #e7e7e7;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 70px;
  color: #b6b6b6;
}

.chronoDragPicture {
  width: 300px;
  border-radius: 12px;
  background-color: #fff;
}

.chronoDragPictureActive {
  outline: 7px solid #1d65ffa4;
}

.chronoDropBoxNumbers {
  position: absolute;
  pointer-events: none;
}

@media screen and (max-width: 1370px) {
  .chronoDragPicture {
    width: 250px;
  }

  .chronoDropBox {
    width: 250px;
    height: 171px;
  }

  .cronoDropPlace {
    width: 110%;
  }
}
