.lessonContent_slider {
  width: inherit;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.sliderContent {
  width: auto;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
}

.sliderContent_slide {
  padding: 20px 0;
  display: flex;
  flex-wrap: nowrap;
  place-content: stretch flex-end;
  align-items: center;
  width: 50vw;
  height: 50vh;
  overflow: hidden auto;
  justify-content: center;
}

.sliderContent_button {
  width: 50px;
  height: 50px;
  background-color: #dde7fd;
  border-radius: 25px;
  position: relative;
  transition: all 0.2s;
  cursor: pointer;
}

.leftButton {
  user-select: none;
  margin: 0 1vw 0 0;
}

.rightButton {
  user-select: none;
  margin: 0 0 0 1vw;
}

.sliderContent_button:hover {
  background-color: #dde7fda1;
}

.sliderContent_sliderCounter {
  margin: 1% 0 0 0;
  font-style: normal;
  font-weight: 300;
  font-size: 18px;
  line-height: 18px;
  color: #000000;
}

.sliderContent_sliderCounter__actual {
  font-weight: 600;
  font-size: 23px;
  color: #000000;
}

.slide {
  height: inherit;
  /* min-width: auto;
  min-height: auto; */
}
