.multiChoisePictureWrapper {
  margin: 30px 0 0 0;
  width: 80%;
  height: 100%;
  justify-content: center;
  align-content: flex-start;
  align-items: flex-start;
}

.multiChoisePicture {
  user-select: none;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  flex-wrap: wrap;
}

.multiChoisePictureCard {
  user-select: none;
  margin: 10px;
  background-color: #ffffff;
  border: none;
  padding: 2px 2px;
  position: relative;
  box-shadow: -5px -5px 15px #ffffff, 5px 5px 15px #e9ecf4;
  border-radius: 8px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 27%;
  height: 30%;
  transition: all 0.2s ease;
}

.multiChoisePictureCard img {
  pointer-events: none;
  max-width: 15vw;
  max-height: 16vh;
  border-radius: 12px;
}

.multiChoisePictureCard:hover {
  box-shadow: -5px -5px 15px #b8b7b7, 5px 5px 15px #99999b;
  cursor: url(img/legCursorMin.png), auto;
  transition: all 0.2s ease;
}

.multiChoisePictureCard:active {
  box-shadow: -5px -5px 15px #ecebeb, 5px 5px 15px #c3c6ce;
  cursor: url(img/legCursorActive.png), auto;
  transition: all 0.2s ease;
}

.multiChoisePictureCardActive {
  box-shadow: none;
  outline: 7px solid #1d65ffa4;
  transition: all 0.2s ease;
}
